@include tablet {
  .delivery-or-pickup {
    &__cloud-desc {
      width: 100%;
      max-width: none;
      margin-bottom: 18px;
    }

    &__login {
      width: auto;
      min-width: 200px;
      padding-right: 20px;
      margin: 0;
      margin-bottom: 8px;
    }

    &__password {
      width: auto;
      margin: 0;
      margin-right: auto;
    }

    &__password + form {
      width: 100%;
      margin-bottom: 20px;
    }
  }
}

@include mobile {
  .delivery-or-pickup__change-data {
    display: none;

    &.mobile {
      display: block;
      margin-bottom: 14px;
    }
  }

  .delivery-or-pickup__wrapper {
    position: relative;
    padding: 17px;
  }

  .delivery-or-pickup__details {
    flex-direction: column;
  }

  .delivery-or-pickup__detail {
    margin-bottom: 5px;
  }

  .delivery-or-pickup__title-block {
    flex-direction: column;
    align-items: flex-start;
    padding-right: 26px;
    margin-bottom: 28px;
  }

  .delivery-or-pickup__change span {
    position: absolute;
    top: 17px;
    right: 13px;
  }

  .delivery-or-pickup__change {
    margin-top: 14px;
  }

  .delivery-or-pickup__confirm {
    margin-top: 10px;
  }

  .delivery-or-pickup__confirm-btn-block {

    button {
      width: 100%;

      &:not(:first-child) {
        margin: 15px 0 0;
      }
    }
  }

  .delivery-or-pickup__payment-link {
    margin-top: 5px;
    padding: 12px;
    margin-bottom: 14px;

    & .info-block__desc {
      word-break: break-word;
      margin-bottom: 20px;
    }

    & form {
      width: 100%;
    }
  }

  .delivery-or-pickup__send {
    width: 100%;
  }

  .delivery-or-pickup__installation-choose-block {
    flex-direction: column;
  }

  .delivery-or-pickup__installation-type {
    &:not(:last-child) {
      margin-bottom: 24px;
    }
  }

  .delivery-or-pickup__installation-cost-wrapper {
    & > div {
      width: 100%;
      margin: 0 0 28px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  .delivery-or-pickup__address-manual {
    & > div {
      width: 100%;
      margin: 0 0 28px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  .delivery-or-pickup__installation-desc {
    margin-bottom: 28px;
  }

  .delivery-or-pickup__installation-confirm {
    padding-left: 5px;
    padding-right: 5px;
  }

  .delivery-or-pickup__fill {
    width: 100%;
  }

  .delivery-or-pickup__call {
    &-block {
      flex-direction: column;
      padding: 17px;
      background-position: top 17px left 17px;
    }

    &-desc {
      padding-left: 40px;
      padding-right: 0;
      margin-bottom: 15px;
      flex-grow: 1;
      width: 100%;
    }

    &-btn-block {
      width: 100%;

      button {
        width: 100%;

        &:not(:first-child) {
          margin: 15px 0 0;
        }
      }
    }

    &-link {
      width: 100%;
    }
  }

  .delivery-or-pickup__alert {
    &-block {
      flex-direction: column;
      padding: 17px;
      background-position: top 17px left 17px;
    }

    &-desc {
      padding-left: 40px;
      padding-right: 0;
      margin-bottom: 15px;
      flex-grow: 1;
      width: 100%;
    }

    &-link {
      width: 100%;
      margin-top: 16px;
    }
  }
}

