*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  height: 100%;
}

body {
  min-width: 320px;
  min-height: 100vh;
  margin: 0 auto;
  font-family: "Suisse Intl", "Arial", "Helvetica", "Helvetica CY", "Nimbus Sans";
  font-weight: 400;
  font-style: normal;
  display: flex;
  flex-direction: column;
}

body.js__body-no-scroll {
  overflow: hidden;
}

main {
  flex-grow: 1;
  padding-top: 124px;
  padding-bottom: 100px;
}

@media (max-width: 999px) {
  main {
    padding-top: 108px;
    padding-bottom: 40px;
  }
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  border: 0;
  margin: -1px;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

input:not([type=checkbox]):not([type=radio]),
textarea,
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

input::-ms-clear {
  display: none;
}

input:-moz-focusring {
  outline: none;
  border-color: #00adef;
  box-shadow: none;
}

input:invalid {
  outline: none;
  box-shadow: none;
}

ul li {
  list-style: none;
}

a {
  border-bottom: 0;
}

.page-body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.wrapper {
  max-width: 1600px;
  margin: 0 auto;
  padding-left: 40px;
  padding-right: 40px;
}

@media (max-width: 999px) {
  .wrapper {
    padding-left: 24px;
    padding-right: 24px;
  }
}

* {
  outline: none;
}

.overflow-3lines {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -moz-box;
  -moz-box-orient: vertical;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  line-clamp: 3;
  box-orient: vertical;
}

.btn-primary,
.btn-secondary,
.btn-secondary-arrow-v {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  display: inline-block;
  height: auto;
  padding: 12px 16px 11px;
  margin: 0;
  text-decoration: none;
  text-align: center;
  border: none;
  border-radius: 5px;
  box-sizing: border-box;
  transition: all .2s ease;
}

.btn-primary {
  background-color: #2858FF;
  color: #fff;
}

.btn-primary:hover, .btn-primary:focus {
  background-color: #113EE0;
  color: #fff;
  cursor: pointer;
}

.btn-primary:active {
  background-color: #113EE0;
  color: #fff;
}

.btn-primary:disabled {
  opacity: 0.5;
}

.btn-primary:disabled:hover {
  background-color: #2858FF;
  cursor: default;
}

.btn-secondary,
.btn-secondary-arrow-v {
  padding: 10px 16px 9px;
  border: 2px solid #2858FF;
  color: #1A1A1A;
  background-color: transparent;
}

.btn-secondary:hover, .btn-secondary:focus,
.btn-secondary-arrow-v:hover,
.btn-secondary-arrow-v:focus {
  color: #2858FF;
  background-color: transparent;
  cursor: pointer;
}

.btn-secondary:hover svg,
.btn-secondary:focus svg,
.btn-secondary-arrow-v:hover svg,
.btn-secondary-arrow-v:focus svg {
  fill: #2858FF;
}

.btn-secondary:active,
.btn-secondary-arrow-v:active {
  color: #2858FF;
  background-color: transparent;
}

.btn-secondary:active svg,
.btn-secondary-arrow-v:active svg {
  fill: #2858FF;
}

.btn-secondary:disabled,
.btn-secondary-arrow-v:disabled {
  opacity: 0.5;
}

.btn-secondary:disabled:hover,
.btn-secondary-arrow-v:disabled:hover {
  color: #1A1A1A;
  cursor: default;
}

.btn-secondary-arrow-v {
  padding-top: 7px;
  padding-bottom: 5px;
}

.btn-secondary-arrow-v * {
  display: inline-block;
  vertical-align: middle;
}

.btn-secondary-arrow-v svg {
  transform: rotate(90deg);
}

.btn-secondary-arrow-v.opened svg {
  transform: rotate(270deg);
}

.btn-arrow,
.btn-arrow-v {
  text-decoration: none;
  color: inherit;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #2858FF;
  display: flex;
  align-items: center;
  padding: 0;
  border: none;
  background-color: transparent;
  transition: all .2s ease;
}

.btn-arrow:hover, .btn-arrow:focus,
.btn-arrow-v:hover,
.btn-arrow-v:focus {
  color: #113EE0;
  cursor: pointer;
}

.btn-arrow svg,
.btn-arrow-v svg {
  margin-left: 4px;
  fill: #2858FF;
  transition: all .2s ease;
}

.btn-arrow-v svg {
  transform: rotate(90deg);
}

.btn-arrow:hover svg,
.btn-arrow-v:hover svg {
  fill: #113EE0;
}

.btn-arrow-v.opened svg {
  transform: rotate(270deg);
}

.btn-text {
  margin: 0;
  padding: 0;
  height: auto;
  border: none;
  background-color: transparent;
  font-size: 14px;
  line-height: 17px;
  font-weight: 500;
  color: #2858FF;
  transition: all 0.3s;
}

.btn-text:hover, .btn-text:active, .btn-text:focus {
  background-color: transparent;
}

.btn-text:hover, .btn-text:focus {
  cursor: pointer;
  color: #113EE0;
}

