.select2-container {
  display: block;
  margin: 0 0 16px;
  font-family: "Suisse Intl", "Arial", "Helvetica", "Helvetica CY", "Nimbus Sans";
  font-size: 14px;
  font-size: .875rem;
  text-align: left;
  text-transform: none;
