  }
  .header__nav.js-show {
    opacity: 1;
    visibility: visible;
    transition-delay: 0s;
  }
  .header__nav.js-show .nav__list {
    transform: translateY(0);
  }
  .header__nav--enterance {
    visibility: visible;
    opacity: 1;
    position: static;
    height: auto;
    overflow: unset;
  }
  .header__nav--enterance .nav__list {
    transform: none;
