  .header__nav-btn span:nth-child(2) {
    top: 50%;
    transform: translateY(-50%);
    transition: height 0.3s;
  }
  .header__nav-btn span:last-child {
    bottom: 4px;
    transition: transform 0.3s;
    transform-origin: bottom left;
