  line-height: 17px;
  font-weight: 400;
  color: #94A0B1;
  flex-shrink: 0;
}

.report-stages__item::before {
  content: "";
  display: inline-block;
  flex-shrink: 0;
  width: 8px;
  height: 8px;
  margin-top: 4px;
  margin-right: 16px;
  margin-left: 8px;
  border-radius: 50%;
