}

.report-stages__item.current {
  color: #1A1A1A;
  font-weight: 600;
}

.report-stages__item.current::before {
  background-color: #2858FF;
}

.report-stages__item.done {
  color: #94A0B1;
}

.report-stages__item.done::before {
  width: 24px;
  height: 24px;
  margin-top: -4px;
  margin-right: 8px;
  margin-left: 0;
  border-radius: 0;
  background-color: transparent;
  background-image: url("../img/check.svg");
}

.report-stages__item.done + .report-stages__line {
  background-color: #2858FF;
}

.report-stages__item.pending {
  color: #1A1A1A;
  font-weight: 600;
}

.report-stages__item.pending::before {
  width: 24px;
  height: 24px;
  margin-top: -4px;
  margin-right: 8px;
  margin-left: 0;
  border-radius: 0;
  background-color: transparent;
  background-image: url("../img/time.svg");
}

.report-stages__item.alert {
  color: #FC4242;
  font-weight: 600;
}

.report-stages__item.alert::before {
  width: 24px;
  height: 24px;
  margin-top: -4px;
  margin-right: 8px;
  margin-left: 0;
  border-radius: 0;
  background-color: transparent;
  background-image: url("../img/alert.svg");
}

.report-stages__line {
  height: 2px;
  width: 1px;
  margin: 7px 24px 0;
  flex-grow: 1;
  background-color: #D1D8E1;
}

@media (max-width: 999px) {
  .report-stages__container {
