.documents {
  margin-bottom: 24px;

  &__title {
    @include section-title;
    color: $text-main;
    margin-bottom: 24px;
  }

  &__list {
    margin: 0;
    padding: 0;
  }

  &__item:not(:last-child) {
    margin-bottom: 14px;
  }
}

@include mobile {
  .documents {
    padding-top: 28px;
  }

  .documents--report {
    padding-top: 0;
  }
}
